import React, { useState, useEffect, useCallback } from 'react';
import R0 from './R0.svg';
import LOGO from './logo.svg';
import THANKS from './thanks.svg';
import './App.css';
import Charts from './Charts';
import { Radio, Row, Col, } from 'antd';
import { FundViewOutlined, SearchOutlined, LayoutOutlined, MessageOutlined, GithubOutlined } from '@ant-design/icons';


function App() {
  const [streamline, setStreamline] = useState('twentyFive');
  const [windowPageYOffset, setWindowPageYOffset] = useState(0);
  const scroll = useCallback(() => {
    setWindowPageYOffset(window.pageYOffset);
  }, [window]);
  function onChange(e) {
    setStreamline(e.target.value);
  }
  useEffect(() => {
    window.addEventListener('scroll', scroll);
    return () => window.removeEventListener('scroll', scroll);
  }, [scroll]);
  return (
    <div className="App">
      <header className={windowPageYOffset > 48 ? 'min' : 'max'} >
        <div className="header-layout">
          <div className="App-title">
            <img src={LOGO} alt="新冠及其他传染病"/>
          </div>
          <a href="https://github.com/aute/Visualization-infectious-diseases"> <GithubOutlined style={{fontSize:'2rem',color:'#fff'}}/></a>
        </div>
      </header>
      <main>
        <h1 className="App-chart-title">病死率及传染性</h1>
        <Radio.Group onChange={onChange} defaultValue="twentyFive">
          <Radio.Button value="twentyFive">25 项</Radio.Button>
          <Radio.Button value="all">更完整</Radio.Button>
        </Radio.Group>
        <div className="App-chart">
          <Charts streamline={streamline} />
        </div>
        <article className="App-article">
          <Row gutter={[16, 16]}>
            <Col className="gutter-row" md={12} sm={24}>
              <div className="App-R0-instructions">
                <img src={R0} className="R0" alt="R0" />
                <div style={{ backgroundColor: '#000' }}><article className="App-R0-instructions-article">
                  <p>
                    <strong>什么是 R0（零） ？</strong> R0 是英文 Basic reproduction number 的简写，中文为基本传染数。是在流行病学上，指在没有外力介入，同时所有人都没有免疫力的情况下，一个感染到某种传染病的人，会把疾病传染给其他多少个人的平均数。
                  </p>
                  <p>
                    <strong>在没有防疫的情况下: </strong>
                  </p>
                  <p>
                    <strong>若 R0 {`<`} 1，</strong>传染病将会逐渐消失。
                    </p>
                  <p>
                    <strong>若 R0 {`>`} 1，</strong>传染病会以指数方式散布，成为流行病（epidemic）。但是一般不会永远持续，因为可能被感染的人口会慢慢减少。部分人口可能死于该传染病，部分则可能病愈后产生免疫力。
                    </p>
                  <p>
                    <strong>若 R0 = 1，</strong>传染病会变成人口中的地方性流行病。
                  </p>
                </article></div>


              </div>
            </Col>
            <Col className="gutter-row" md={12} sm={24}>
              <div className="App-chart-instructions">
                <h2>操作提示</h2>
                <ul>
                  <li>
                    <h4><FundViewOutlined /> 观看设备</h4>
                    数据展示类应用，在更大屏幕上可获得更好的观看及操作体验。
              </li>
                  <li>
                    <h4><SearchOutlined /> 数据筛选</h4>
                    上方切换按钮，在经大众所熟知的 25 项传染病与应用所收集到所有传染病数据间进行切换。通过图例可高亮或关闭此类展示。
              </li>
                  <li>
                    <h4><LayoutOutlined /> 缩放与拖动</h4>
                    图表默认展示死亡率小于 20%、R0 小于 20 数据项。通过控制条可分别进行「死亡率」及「传染性」在其他范围拖动与缩放。同时，在数据展示区域，通过鼠标按键或滚轮或单指及双指手势，也可实现拖动与缩放操作。
              </li>
                  <li>
                    <h4><MessageOutlined /> 更多内容</h4>
                    点击具体项 ● 可浮窗展示更「传染方式」等多内容。
              </li>
                </ul>
              </div>
            </Col>
          </ Row>
        </article>
      </main>
      <footer>
        <h5>BY AUTE</h5>
        <img src={THANKS} alt="*感谢 Marlbor、遇见风、西门吹水、Angie2000、瞳、 天生痞子相、何足道在哉、拉里、魏凱傑、平三、只有头发像马努 为数据筛选提供支持"/>
      </footer>
    </div>
  );
}

export default App;
