import React, { useState, useEffect } from "react";
import ReactEcharts from "echarts-for-react";
import * as R from 'ramda';
import microbe_scope_bugs_data from "./data/microbe_scope_bugs";
import { getChartOption, famous_microbe_zh_map } from "./utils";

const Chart = (props) => {
  const [option, setOption] = useState(getChartOption(
    R.filter(i => {
      return famous_microbe_zh_map[i.DISEASE__1];
    }, microbe_scope_bugs_data),
  ));
  useEffect(() => {
    if (props.streamline==='twentyFive') {
      setOption(getChartOption(
        R.filter(i => {
          return famous_microbe_zh_map[i.DISEASE__1];
        }, microbe_scope_bugs_data),
      ))
    } else{
      setOption(getChartOption(microbe_scope_bugs_data))
    }
}, [props.streamline]);
  return (
    <div style={{ height: "680px", maxHeight: "72vh", minHeight: "360px" }}>
      <ReactEcharts
        notMerge={true}
        option={option}
        lazyUpdate={true}
        style={{ width: "100%", height: "100%" }}
      />
    </div>
  );
};

export default Chart;